* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	height: 100vh;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
  background: url(https://i.postimg.cc/4NqVcPSm/SP1-Space-Land-Collection.jpg) no-repeat center fixed;
  background-size: cover;
}

.noselect {
  -webkit-touch-callout: none;
    -webkit-user-select: none;
     -khtml-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
		-webkit-tap-highlight-color: transparent;
}

button {
	width: 190px;
	height: 40px;
	cursor: pointer;
	background: rgba(0, 0, 0, 0.600);
	margin: 10px;
	border: none;
	border-radius: 10px;
	color: rgb(255, 251, 251);
	font-size: 16px;
}

button:hover {
	font-size: 14px;
	transition: 500ms;
}